import React from "react";
import { Card } from "react-native-paper";
import { Text, View, Image, ScrollView } from "react-native";
import { Button, ProgressBar } from "react-native-paper";
import { Gift, Trophy, Users, Star, Book, Award } from "lucide-react-native";

const user = {
  name: "Timothy Olimba",
  totalPoints: 15000,
  redeemedPoints: 7000,
  availablePoints: 8000,
  expiryDate: "2025-12-31",
  tier: "Diamond",
};

const rewardsCatalog = [
  { name: "Free Textbook", points: 3000, image: require("../../assets/textbook.png") },
  { name: "Canteen Meal Voucher", points: 2000, image: require("../../assets/meal.png") },
 
  { name: "Cisco Certification Exam Voucher", points: 5000, image: require("../../assets/cisco.jpeg") },
  { name: "One-on-One Mentorship Session", points: 4000, image: require("../../assets/mentor.png") },
];

const transactions = [
  { date: "2025-02-20", description: "Attended 10 Classes", points: "+1000" },
  { date: "2025-02-15", description: "Redeemed: Cisco Certification Exam Voucher", points: "-5000" },
  { date: "2025-02-10", description: "Redeemed: Free Textbook", points: "-3000" },
  { date: "2025-01-25", description: "Completed a Quiz", points: "+500" },
];

const RewardsPage = () => {
  return (
    <ScrollView style={{ padding: 16 }}>
      <View style={{ flexDirection: "row", flexWrap: "wrap", justifyContent: "space-between" }}>
        <Card style={{ padding: 16, margin: 8, flex: 1 }}>
          <Trophy color="#212E5B" size={32} />
          <Text>Total Points</Text>
          <Text>{user.totalPoints}</Text>
        </Card>
        <Card style={{ padding: 16, margin: 8, flex: 1 }}>
          <Gift color="#212E5B" size={32} />
          <Text>Redeemed Points</Text>
          <Text>{user.redeemedPoints}</Text>
        </Card>
        <Card style={{ padding: 16, margin: 8, flex: 1 }}>
          <Star color="#212E5B" size={32} />
          <Text>Available Points</Text>
          <Text>{user.availablePoints}</Text>
        </Card>
        <Card style={{ padding: 16, margin: 8, flex: 1 }}>
          <Users color="#212E5B" size={32} />
          <Text>Tier Level</Text>
          <Text>{user.tier}</Text>
        </Card>
      </View>
      
      <Text style={{ fontSize: 18, fontWeight: "bold", marginTop: 16 }}>Rewards Catalog</Text>
      {rewardsCatalog.map((reward, index) => (
        <Card key={index} style={{ padding: 16, margin: 8 }}>
          <Image source={reward.image} style={{ width: 50, height: 50 }} />
          <Text>{reward.name}</Text>
          <Text>{reward.points} Points</Text>
          <Button mode="contained" color="#212E5B">Redeem</Button>
        </Card>
      ))}
      
      <Text style={{ fontSize: 18, fontWeight: "bold", marginTop: 16 }}>Recent Transactions</Text>
      {transactions.map((transaction, index) => (
        <Card key={index} style={{ padding: 16, margin: 8 }}>
          <Text>{transaction.date}</Text>
          <Text>{transaction.description}</Text>
          <Text style={{ color: transaction.points.startsWith("+") ? "green" : "red" }}>{transaction.points}</Text>
        </Card>
      ))}
      
      <Text style={{ fontSize: 18, fontWeight: "bold", marginTop: 16 }}>Class Attendance Bonus</Text>
      <Card style={{ padding: 16, margin: 8 }}>
        <Book color="#212E5B" size={32} />
        <Text>Earn points by attending classes! Every session adds to your rewards, helping you unlock exclusive benefits.</Text>
      </Card>

      <Text style={{ fontSize: 18, fontWeight: "bold", marginTop: 16 }}>Tier Progress</Text>
      <Card style={{ padding: 16, margin: 8 }}>
        <Text>You're at the Diamond Tier. Keep attending classes and earning points to maintain your elite status!</Text>
        <ProgressBar progress={user.totalPoints / 20000} color="#212E5B" />
      </Card>
    </ScrollView>
  );
};

export default RewardsPage;
